                  <el-input v-model="params.password"></el-input>
                </el-form-item>
                <el-form-item label="年龄">
                  <el-input v-model="params.age"></el-input>
                </el-form-item>
                <el-form-item label="身份证">
                  <el-input v-model="params.idCard"></el-input>
                </el-form-item>
                <el-form-item label="性别">
                  <el-radio-group v-model="params.gender">
                    <el-radio :label="0">男</el-radio>
                    <el-radio :label="1">女</el-radio>
                  </el-radio-group>
                </el-form-item>
                
