                <el-upload
                    :action="upload()"
                    class="avatar-uploader"
                    :show-file-list="false"
                    :on-remove="removeImg"
                    :on-success="handleAvatarSuccess"
                    :before-upload="beforeAvatarUpload">
                    <img v-if="imageUrl" :src="imageUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
                </el-form-item> -->

                <!-- <el-form-item label="成就达标">
                  <el-switch v-model="params.achievement"></el-switch>
                </el-form-item>
                <el-form-item label="兴趣爱好">
                  <el-checkbox-group v-model="hoba" @change="abc">
                    <el-checkbox v-for="hob in hobby" :key="hob.code" :label="hob.code">{{hob.name}}</el-checkbox>
                  </el-checkbox-group>
                </el-form-item>
                
                <el-form-item label="个人宣言">
                  <el-input type="textarea" v-model="params.desc"></el-input>
                </el-form-item> -->
                
                <el-form-item>
