    },
    methods: {
      
     
      // handleAvatarSuccess(res, file) {
      //   console.log("res：",res);
      //   if(res.code=200){
      //     this.imageUrl = this.$baseUrl()+res.data;
      //     this.params.avatar = res.data;
      //     this.$message({
      //           message: '上传图片成功',
      //           type:'success',
      //           duration:1*1000
      //     });
      //   }else{
      //     this.$message({
      //           message: res.message,
      //           type:'error',
      //           duration:1*1000
      //     });
      //   }
