       onSubmit() {
        console.log('submit:',this.params);
        let formdata = {
          id:this.id,
          phone:this.params.phone,//手机号
          password:this.params.password,//密码
          name:this.params.name,//姓名
          age:this.params.age,//年龄
          gender:this.params.gender,//性别(0:男,1:女)
          idCard:this.params.idCard,
          level:this.params.level
        }
        this.axios.post("user/change",this.$qs.stringify(formdata)).then(res=>{
          console.log("res222:",res);
          if(res.data.code==200){
